<template>
	<div>
		<h1>一键生成 视频导航素材.mp4</h1>
		<h3 v-if='!creating'>bilibili冬灰条 制作</h3>
		<div v-else>
			<h3 style="color:blue">
				<span>正在录制生成素材，倒计时</span>
				<span v-text="waitTime"></span>
				<span>秒</span>
			</h3>
		</div>
		<canvas id="myCanvas" :width='vedioWidth' :height='navHeight'></canvas>
		<!-- 底部颜色 -->
		<div class="flex justify-center align-center colorItem">
			<div class="title">底部颜色</div>
			<el-color-picker v-model="baseColor" @active-change='changeBaseColor'></el-color-picker>
		</div>
		<!-- 进度颜色 -->
		<div class="flex justify-center align-center colorItem">
			<div class="title">进度颜色</div>
			<el-color-picker v-model="processColor" @active-change='changeProcessColor'></el-color-picker>
		</div>
		<!-- 文本 -->
		<div class="flex justify-center align-center colorItem">
			<div class="title">文本颜色</div>
			<el-color-picker v-model="textColor" @active-change='changeTextColor'></el-color-picker>
			<div class="title" style="margin-left:30px">文本大小</div>
			<el-input-number
				v-model="fontSize"
				controls-position="right"
				:min="12"
				:max="45"
				@change="updateCanvas()"
			>
			</el-input-number>
		</div>
		<!-- 边框颜色 -->
		<div class="flex justify-center align-center colorItem">
			<div class="title">边框颜色</div>
			<el-color-picker v-model="borderColor" @active-change='changeBorderColor'></el-color-picker>
			<div class="title" style="margin-left:30px">边框宽度</div>
			<el-input-number
				v-model="lineWidth"
				controls-position="right"
				:min="1"
				:max="9"
				@change="updateCanvas()"
			>
			</el-input-number>
		</div>
		<!-- 一个环节 -->
		<div v-for="(part,index) in arr" :key="index" class="onePart">
			<h3 v-text="index+1"></h3>
			<el-input @input="changeText(index)" v-model="part.name" class="partName" placeholder='请输入环节名称  如 : 开头介绍'></el-input>
			<!-- 文本颜色 -->
			<el-color-picker v-model="part.textColor" @active-change='changePartTextColor($event,index)' style="margin-right:30px"></el-color-picker>
			<!-- 如果是第一个环节，那么最低就是0 -->
			<!-- 如果是第二个环节以上，那么最低就是上一个环节的end的minites -->
			<el-input-number
				v-model="part.end.minites"
				controls-position="right"
				:min="getMinitesMin(index)"
				@change="changeNumber(index)"
			>
			</el-input-number>
			<div style="margin:0 21px;">分钟</div> 
			<!-- 如果是第一个环节，那么最低就是0 -->
			<!-- 如果是第二个环节以上，如果和上一个环节的end的minites相等，那么最低就是上一个环节的seconds -->
			<!-- 如果是第二个环节以上，如果和上一个环节的end的minites不等，那么最低就是0 -->
			<el-input-number
				v-model="part.end.seconds"
				controls-position="right"
				:min="getSecondsMin(index)"
				:max="60"
				@change="changeNumber(index)"
			>
			</el-input-number>
			<div style="margin:0 21px;">秒</div>
			<!-- 删除按钮 -->
			<i v-if="!creating" 
			   class="el-icon-delete-solid deleteOnePart"
			   @click="deleteOnePart(index)"
			></i>
			<div v-else style="width:21px"></div>
		</div>
		<el-button @click="addOnePart" :disabled='creating'>增加一个环节</el-button>
		<div style="margin-top:30px">
			<el-button v-if="!creating" :disabled='creating' type='success' @click="getNavVedio">一键生成视频导航.mp4</el-button>
			<el-button v-else type='danger' @click="cancelGetNav">取消生成</el-button>

			
			<el-button :disabled='creating' @click="openVideo">查看相关视频</el-button>
		</div>
	</div>
</template>

<script>
import ysFixWebmDuration from './../assets/fix-webm-duration'
export default {
	name: "HelloWorld",
	mounted(){
		window.resizeTo(3000, 600);
		 // 获取画布
        let canvas = document.getElementById("myCanvas");
        // 创建上下文
		this.ctx= canvas.getContext("2d");
		
        // 画一个背景
        this.ctx.fillStyle = this.baseColor;
        this.ctx.fillRect(0,0,this.vedioWidth,this.navHeight);
        // 画一个边框
        this.ctx.lineWidth = this.lineWidth*2;
        this.ctx.strokeStyle = this.borderColor;
		this.ctx.strokeRect(0,0,this.vedioWidth,this.navHeight);
		this.drawText(this.lineWidth,0);
	},
	data() {
		return {
			num: 0,
			ctx:null,
			startTime:null,
			baseColor:'#000000',
			processColor:'#00CCFF',
			borderColor:'#409EFF',
			textColor:'#FFFFFF',
			creating:false,
			fontSize:30,
			arr:[
				{
					name:'开头介绍',
					end:{
						minites:0,
						seconds:30
					},
					textColor:''
				},
				{
					name:'中间展示',
					end:{
						minites:1,
						seconds:30
					},
					textColor:''
				},
				{
					name:'结尾部分',
					end:{
						minites:2,
						seconds:0
					},
					textColor:''
				},
			],
			vedioWidth:1920,
			navHeight:54,
			lineWidth:3,
			waitTime:0,
			mediaRecord:null,
			videoUrl:null,
			chunks:null,
			timer:null,
			cancel:false
		};
	},
	computed:{
		allSeconds(){
			return this.arr[this.arr.length-1].end.minites*60+this.arr[this.arr.length-1].end.seconds
		},
	},
	methods:{
		// 查看相关视频
		openVideo(){
			window.open("https://www.bilibili.com/video/BV1vf4y1v7MV"); 
		},
		// 修改环节名称
		changeText(index){
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		changePartTextColor(e,index){
			this.$set(this.arr[index],'textColor',e);
			this.updateCanvas();
		},
		// 清空画布
		clearCanvas(){
			// 清空画布
			this.ctx.clearRect(0,0,this.vedioWidth,this.navHeight);
			// 不知道为什么，只有运行下面这两句才能清理画布的线
			let canvas = document.getElementById("myCanvas");
			canvas.width = canvas.width
			this.ctx.fillStyle = this.baseColor;
			this.ctx.fillRect(0,0,this.vedioWidth,this.navHeight);
			// 画一个边框
			this.ctx.lineWidth = this.lineWidth*2;
			this.ctx.strokeStyle = this.borderColor;
			this.ctx.strokeRect(0,0,this.vedioWidth,this.navHeight);
		},
		// 给进度条填入文字
		drawText(startPosition,index){
			// 获取本环节的时长
            let partSeconds = this.getPartSeconds(index);
            // 获得本环节该占多少像素
            let partLength = (partSeconds * this.vedioWidth)/this.allSeconds;
            // 获得结束时的位置
			let endPosition = startPosition+partLength+3;
			this.ctx.font = `${this.fontSize}px bold`;
            this.ctx.textAlign = 'center';
			this.ctx.textBaseline = 'middle';
			this.ctx.fillStyle =this.arr[index].name==''?this.borderColor:this.textColor;
			if(this.arr[index].textColor!='') this.ctx.fillStyle = this.arr[index].textColor;
            this.ctx.fillText(this.arr[index].name==""?'环节名称':this.arr[index].name,startPosition+(partLength/2), this.navHeight/2);
            this.ctx.moveTo(endPosition,this.lineWidth);
            this.ctx.lineTo(endPosition,this.navHeight-this.lineWidth);
            this.ctx.lineWidth = this.lineWidth;
            this.ctx.strokeStyle = this.borderColor;
			this.ctx.stroke();
            if(index<this.arr.length-1) this.drawText(endPosition,index+1);
		},
		// 获取分钟的最小值
		getMinitesMin(index){
			if(index==0) return 0;
			return this.arr[index-1].end.minites;
		},
		// 获取秒的最小值
		getSecondsMin(index){
			if(index==0) return 0;
			if(this.arr[index-1].end.minites==this.arr[index].end.minites) return this.arr[index-1].end.seconds;
			return 0
		},
		// 获取时间的时长，单位为秒
		getTimeLength(time){
			return time.minites*60+time.seconds;
		},
		// 获取环节时间长度，单位为秒
		getPartSeconds(index){
			if(index==0) return this.getTimeLength(this.arr[index].end);
			return this.getTimeLength(this.arr[index].end)-this.getTimeLength(this.arr[index-1].end);
		},
		// 增加一个环节
		addOnePart(){
			//增加时，复制上一个环节的时间
			let end = JSON.parse(JSON.stringify(this.arr[this.arr.length-1].end));
			this.arr.push({
				name:'',
				end
			});
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		// 当删除一个环节时
		deleteOnePart(index){
			this.arr.splice(index,1)
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		// 当修改时间时
		changeNumber(index){
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
			/*
				如果修改的不是最后一项
				如果这个环节的end比下一个环节的end大
				那就直接修改下一个环节的end
			*/
			if(index!=this.arr.length-1
			&&
			this.getTimeLength(this.arr[index].end)>this.getTimeLength(this.arr[index+1].end)){
				this.arr[index+1].end = JSON.parse(JSON.stringify(this.arr[index].end));
				this.changeNumber(index+1);
			}
		},
		download(blob){
			// this.mediaRecord.stop() // 结束录屏
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
			// const videoBlob = new Blob(this.chunks, { 'type': 'video/mp4' })   // 创建视频文件
			this.videoUrl = window.URL.createObjectURL(blob)    // 创建对象链接
			let a = document.createElement('a')
			a.href = this.videoUrl
			a.download = '冬灰条帮你自动生成的进度条.mp4'
			a.style.display = 'none'
			document.body.appendChild(a)
			a.click()
		},
		// 修改背景底色
		changeBaseColor(e){
			this.baseColor = e;
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		// 修改进度颜色
		changeProcessColor(e){
			this.processColor = e;
		},
		// 修改边框颜色
		changeBorderColor(e){
			this.borderColor = e;
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		changeTextColor(e){
			this.textColor = e;
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		// 修改边框宽度
		updateCanvas(){
			this.clearCanvas();
			this.drawText(this.lineWidth,0);
		},
		cancelGetNav(){
			this.cancel = true;
			this.waitTime = 10;
			clearInterval(this.timer);
			this.creating = false;
			this.updateCanvas();
			this.mediaRecord.stop()
		},
		//当生成素材时
		getNavVedio(){
			// 开头这部分是直接从网上抄的代码
			this.chunks = new Set() // 先准备个容器保存canvas来的图像流
			let createRecord = ()=>{
				this.creating = true;
				const mediaStream = document.querySelector('#myCanvas').captureStream(48) 
				// 获取画布canvasElement并设置帧频率(FPS)
				this.mediaRecord = new MediaRecorder(mediaStream, {   
					// 核心API，可以录制canvas, audio, video代码下方提供文档链接
					videoBitsPerSecond: 850000,
					mimeType: "video/webm"
				})
				this.mediaRecord.blobs = [];
				this.mediaRecord.ondataavailable = (e) => { // 接收数据
					this.mediaRecord.blobs.push(e.data);
					this.chunks.add(e.data)
					// this.chunks.push(e.data);
				}
				this.mediaRecord.onstop = (e)=>{
					if(this.cancel) return setTimeout(() => {
						this.cancel = false;
					}, 500); 
					let duration = Date.now() - this.startTime;//停止录制时候 用当前时间减去开始录制的时间
					let tmpBlob = new Blob(this.chunks, { 'type':this.mediaRecord.mimeType});
					//调用设置时长代码。该函数基于fix-webm-duration.js
					ysFixWebmDuration(tmpBlob, duration, (fixedBlob)=>{
						this.download(fixedBlob)
					});
				};
				this.mediaRecord.start(100)  // 开始录屏,参数为抓取间隔100毫秒
				this.startTime = Date.now();
				timer(step);
			};
			// 以下是自己写的代码
			// 总时长
			// let timeMax = this.allSeconds*1000;
			let timeMax = 10000;
			// 多少毫秒变化一次
			let eachStepTime = 20
			// 每次改变进度条的像素
			let step = (this.vedioWidth-this.lineWidth)/(timeMax/eachStepTime);
            // 改变进度条的次数
			let process = 0;
	
			let height = this.navHeight;
			let videoWidth = this.vedioWidth;
			// 因为发现setInterval有性能问题，所以换成了setTimeout递归
			let timer = (width)=>{
				setTimeout(()=>{
					if(this.cancel) return;
					// 先绘制进度
					this.ctx.fillStyle = this.processColor;
					this.ctx.fillRect(this.lineWidth,this.lineWidth,width,54-(this.lineWidth*2));
					// 然后把分割线与文字画到进度条上
					this.drawText(this.lineWidth,0);
					// 改变次数增加，等待下一次绘制进度
					process++;
					if(width>videoWidth){
						// this.download();
						this.mediaRecord.stop() // 结束录屏
						clearInterval(this.timer);
						this.creating = false;
					}else{
						timer(width+step);
					}
				},eachStepTime)
			}
			this.waitTime = 10;
			this.timer = setInterval(()=>{
				this.waitTime--;
			},1000)
			createRecord();
		}, 
	}
};
</script>

<style scoped>
.title{
	font-weight: bold;
	margin-right: 10px;
}
#myCanvas{
	margin-bottom: 30px;
}
.onePart{
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 30px;
}
.partName{
	width: 240px;
	margin-left: 30px;
}
.deleteOnePart{
	cursor: pointer;
	color: red;
	font-size: 21px;
}
.colorItem{
	margin-bottom: 30px;
}
</style>
